:root {
    --primary: #3acbf7;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover {
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: #fff;
    color: #6568F4;
    transition: 250ms;
}

.btn--primary {
    background-color: white;
    display: flex;
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    font-weight: 500;
    color: #03102F;
    transition: all 0.5s ease;
    border: 2px solid white;
}

.btn--medium:hover {
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
    background: #03102F;
    border: 2px solid white;
    border-radius: 4px;
}


.btn-large {
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
}

@media screen and (max-width: 960px) {
    .btn{
        display: none;
    }
}



