.NavbarItems {
    /* background: ; */
    background:#03102F;
    /* background: red;  */
    height: 60px;
    display: flex;
    justify-content: center !important;
    align-items: center !important;
    position: sticky;
    top:0;
    font-size: 1.2rem;
    z-index: 5 !important;
    padding-right: 30px;
}

.hide{
    margin-bottom: 1rem !important;
}

.navbar-logo {
    font-size: 20px;
    font-family: 'Montserrat';
    font-weight: 600;
    color: #fff;
    /* justify-self: start;
    align-self: center; */
    margin-left: 20px;
    margin-bottom: 0px;
    cursor: pointer;
}


.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    padding-top: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: white;
    font-weight: 600;
    text-decoration: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
}


.nav-links:hover {
    color: #ccc;
    color: rgba(255,255,255,0.5);
    transition: all 0.2s ease-out;
    text-decoration: none;
}

.navlink:hover {
    color: #ccc;
    color: rgba(255,255,255,0.5);
    transition: all 0.2s ease-out;
    text-decoration: none;
}

.fa-bars {
    color: #fff;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {

    /* .navbar-logo {
        margin-top: 10px;
    } */

    .NavbarItems {
        height: 81px;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 108vw;
        height: 270px;
        position: absolute;
        top: 80px;
        padding-top: 0;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #03102F;
        /* background: red; */
        left:-8vw;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        padding-top: 0;
    }

    .nav-links {
        text-align: center;
        padding-left: 2rem;
        padding-right: 2rem;
        /* padding-top: 1rem; */
        /* padding-bottom: 1rem; */
        width: 100%;
        display: table;
    }
    
    .nav-links:hover {
        font-weight: 600;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        margin-top: 10px;
        left: 0;
        margin-left: 0px;
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 16px;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1rem;
        cursor: pointer;
    }

    .fa-times {
        color: #fff;
        font-size: 1.5rem;
    }

    .fa-bars {
        color: #fff;
        font-size: 1.3rem;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: #4ad9e4;
        text-decoration: none;
        color: #fff;
        font-size: 1.5rem;
    }

    .nav-links-mobile:hover {
        background: #fff;
        color: #6568F4;
        transition: 250ms;
    }

    Button {
        display: none ;
        margin-right: 80px !important;
        
    }

}

@media screen and (min-width: 960px) {
    .hide{
        display:none;
    }
}